import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Platform } from '@ionic/angular';
import { DataService } from 'src/app/services/data.service';

@Component({
  selector: 'app-login-landing',
  templateUrl: './login-landing.page.html',
  styleUrls: ['./login-landing.page.scss'],
})
export class LoginLandingPage implements OnInit {
  currentUser: any;
  isFaceId: boolean = false;
  isFingerPrint: boolean = false;

  constructor(private router: Router,
    private platform: Platform,
    private cdr: ChangeDetectorRef,
    private dataService: DataService) { }

  ngOnInit() {
    this.currentUser = this.dataService.getCurrentUser();

    this.dataService.share.subscribe((data: any) => {
      if (data) {
        this.currentUser = this.dataService.getCurrentUser();
        this.cdr.markForCheck();
      }
    })

    this.cdr.markForCheck();
  }

  ionViewWillEnter() {
    this.currentUser = this.dataService.getCurrentUser();
    this.cdr.markForCheck();
  }

  goToLogin() {
    this.router.navigate(['login'])
  }

  goToMpin() {
    this.router.navigate(['mpin'])
  }

  goToFaceId() {
    this.router.navigate(['/faceidrecognition']);
  }

  goToFingerprint() {
    this.router.navigate(['/fingerprint']);
  }

}
